<template>
  <div class="block">
    <el-date-picker
      v-model="pickerTime"
      type="month"
      placeholder="选择月"
      :picker-options="pickerOptions">
    </el-date-picker>
  </div>
</template>

<script>
    export default {
        name: "MonthTimePicker",
      data() {
        return {
          pickerTime: '',
          pickerOptions: {
            disabledDate(time) {
              return time.getTime() > Date.now();
            }
          },
        };
      }
    }
</script>

<style scoped>

</style>



